$grid_base_row: 350px

.agenda__grid-container
  display: grid
  grid-template-areas: "first first second" "first first fifth" "third fourth fifth"
  grid-template-columns: 1fr 1fr 1fr
  grid-template-rows: $grid_base_row $grid_base_row $grid_base_row

  @media screen and (max-width: 414px)
    grid-template-areas: "first" "second" "third" "fourth" "fifth"
    grid-template-columns: 1fr
    grid-template-rows: 1fr


.agenda__grid-item
  -webkit-background-size: cover
  background-size: cover
  background-repeat: no-repeat
  background-position: center center
  background-color: rgba(51,51,51,0.2)
  background-blend-mode: multiply
  .date,
  .title
    font-weight: bold
    font-size: 2rem

.agenda__grid-item-1
  grid-area: first

.agenda__grid-item-2
  grid-area: second

.agenda__grid-item-3
  grid-area: third

.agenda__grid-item-4
  grid-area: fourth

.agenda__grid-item-5
  grid-area: fifth
